@import "https://fonts.googleapis.com/css2?family=Inter:wght@500&family=Montserrat:wght@400;500;600;700;800;900&family=Seymour+One&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  box-sizing: border-box;
}

body {
  color: #131313;
  background-image: url("background.e9fd6ef4.jpg");
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

li, p, button {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.container {
  max-width: 1168px;
  margin: 0 auto;
}

a, address {
  color: #131313;
  cursor: pointer;
  font-style: normal;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  border: none;
}

.wrapper {
  height: 100vh;
  overflow: auto;
}

.modal__submit-form input, .modal__submit-form textarea, .modal-contacts__input, .modal-contacts__textarea {
  background: #f8d24e1a;
  border: 1px solid #84a178;
  border-radius: 10px;
  padding: 8px 48px 8px 12px;
  font-size: 12px;
  line-height: 140%;
}

.modal-location__title, .modal-contacts__title {
  text-align: center;
  padding-bottom: 20px;
  font-family: Seymour One;
  font-size: 16px;
  line-height: 1.5;
}

.is-hidden .modal-helper {
  transform: translate(-50%, -50%)scale(0%)rotate3d(1, 1, 1, 45deg);
}

.modal-hidden {
  display: none;
}

.footer-button__icon, .buy-arrow-in-button {
  pointer-events: none;
}

.backdrop {
  height: 100%;
  width: 100%;
  z-index: 100;
  visibility: visible;
  opacity: 1;
  background-color: #00000073;
  transition: visibility .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  z-index: 0;
}

.header {
  background-color: #84a178;
}

.header-flex__container {
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px 0;
  display: flex;
}

.site-logo {
  fill: #f8d24e;
}

.site-icon-logo {
  width: 40px;
  height: 37px;
}

.site-nav {
  width: 444px;
  display: none;
}

.site-nav__list {
  max-width: 100%;
