  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.site-nav__link {
  color: #fcfcfc;
  padding: 10px 0;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.25;
  text-decoration: none;
  display: inline-block;
}

