  background-color: #0a8817;
  border-radius: 10px;
  padding: 12px 36px;
  font-weight: 600;
  line-height: 18px;
  display: inline-block;
}

.button__buy:focus, .button__buy:focus .menu-toggle {
  color: #fcfcfc;
  fill: #fcfcfc;
  background-color: #0a8817;
  border-color: #0a8817;
}

.button__buy:disabled {
  background-color: #ababab;
  border-radius: 10px;
  padding: 12px 36px;
  font-weight: 600;
  line-height: 18px;
  display: inline-block;
}

.site-button__buy {
  margin-bottom: 5px;
  display: none;
}

.site-button__buy.buy-arrow-in-button {
  margin-left: 4px;
}

@media screen and (min-width: 768px) {
