.hero {
  background-color: $dark-green;
  padding-top: 45px;
  &__wrapper {
    display: flex;
    justify-content: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 32px;

    color: $white;
    font-family: $font-family-sey;
    font-size: 26px;
    line-height: 1.2;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    &--bold {
      font-size: 34px;
      line-height: 0;
    }
  }

  &__link-group {
    display: flex;
    align-items: center;
    margin-bottom: 19px;
  }

  &__link-products {
    display: inline-block;
    margin-right: 14px;
    padding: 11px 26px;

    color: $black;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.2;
    text-decoration: none;

    border: 1px solid;
    border: 2px solid $yellow;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    border-radius: 8px;
    cursor: pointer;
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover {
      border: 2px solid $button-hover;
    }
    &:focus {
      color: $white;
    }
  }

  &__link-about {
    color: $yellow;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.2;
    cursor: pointer;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover {
      color: $button-hover;
    }
    &:focus {
      color: $white;
    }
    &:disabled {
      color: $button-disabled;
    }
  }

  &__advantages {
    display: none;
  }

  &__advantages-text {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 0;
    margin-top: 14px;
    margin-bottom: 20px;
    width: 46px;

    color: $black;
    font-weight: 900;
    font-size: 28px;
    line-height: 1.2;
    letter-spacing: 0.04em;
  }

  &__advantages-text:last-child {
    margin: 0;
  }

  &__advantages-span {
    display: inline-block;
    font-weight: 400;
    margin-top: 4px;

    color: $white;
    font-size: 8px;
    line-height: 1.2;
    letter-spacing: 0;
  }

  &__image-wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }

  &__iceCreamPlate-wrapper {
    position: relative;
  }

  &__image-iceCreamPlate {
    display: none;
  }

  &__text-wrapper {
    display: none;
  }

  &__arrow-link {
    width: 40px;
    cursor: pointer;
  }

  &__arrow {
    display: none;
    fill: $white;
  }

  &__iceCream-wrapper {
    position: relative;
    width: 177px;
    height: 217px;
  }

  &__image-iceCream {
    position: absolute;
    bottom: 0;
    right: -15px;

    z-index: 2;
  }

  &__dots-wrapper {
    width: 320px;
    position: absolute;
    display: flex;
    justify-content: center;
    pointer-events: none;
  }

  &__dots-tablet {
    display: none;
  }

  &__dots-desktop {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .hero {
    &__title {
      margin-bottom: 24px;
      margin-right: 411px;
    }

    &__link-group {
      margin-bottom: 39px;
    }

    &__advantages {
      display: block;
    }

    &__image-wrapper {
      align-items: flex-end;
    }

    &__iceCreamPlate-wrapper {
      position: relative;
      width: 152px;
      height: 100px;

      margin-right: 32px;
    }

    &__image-iceCreamPlate {
      display: inline-block;
      position: absolute;
      bottom: 10px;
      left: 0px;
      width: 100%;
    }

    &__text-wrapper {
      display: inline-block;
      margin-right: 53px;
