.modal-location {
    @include modal-window;
  
    min-width: 280px;
    min-height: 365px;
  }

  .modal-location__title {
    @extend %modal-title;
  }

  .modal-location__img {
    max-width: 448px;
    min-width: 224px;
    margin: 0 auto;
  }

  .modal__button {
    margin-left: auto;
    margin-right: auto;
    margin-top: 24px;
    @include btn-mobile($yellow);
    @include btn-border(2px, $yellow);
    padding: 12px 34px;
    font-size: 12px;
    line-height: 15px;
    transition: background-color 250ms $timing-function, border 250ms $timing-function, color 250ms $timing-function;

     &:hover {
        background-color: $button-hover;
        @include btn-border(2px, $button-hover);
     }
     &:focus {
        background-color: $button-hover;
        @include btn-border(2px, $button-hover);
        color: $white;
     }
     &:disabled {
        background-color: $button-disabled;
        @include btn-border(2px, $button-disabled);
     }
  }

  .modal__close {
    position: absolute;
    top: 12px;
    right: 12px;
    border: none;
    background-color: $white;
  }
  
  @media screen and (min-width: 768px) {

    .modal-localion {
    width: 568px;
    height: 458px;
    padding: 32px 60px;
  }

  .modal__title {
    padding-bottom: 24px;
font-size: 20px;
line-height: 1.4;
  }


  .modal__button {
    padding: 12px 24px;
    font-size: 14px;
line-height: 17px;
  }

  .modal__close {
    width: 24px;
    height: 24px;
  }
}

