@mixin modal-window {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(100%);
  padding: 32px;
  text-align: center;
  color: $black;
  background-color: $white;
  border-radius: 10px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 999;
}

%modal-title {
  padding-bottom: 20px;
  font-family: 'Seymour One';
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
}

.is-hidden .modal-helper {
  transform: translate(-50%, -50%) scale(0%) rotate3d(1, 1, 1, 45deg);
}

.modal-hidden {
  display: none;
}

//helper for svg in button
.footer-button__icon, .buy-arrow-in-button {
  pointer-events: none;
}


